import ItemLink from "./cardItem";

const MainBlock: React.FC = () => {
  return (
    <div className="blocks">
      <div>
        <ItemLink
          img={Fiz}
          link="/fizFace"
          title="  "
          description="      "
        />

        <ItemLink
          img={Premium}
          link="/getTalon"
          isPrem={true}
          title=" "
          description="  ,  ,  "
        />
      </div>
      <div>
        <ItemLink
          img={Money}
          link="/currency"
          title=""
          description=" ,    ,  "
        />
        <ItemLink
          img={Lowyr}
          link="/lowyer"
          title="  "
          description="      "
        />
      </div>
    </div>
  );
};

export default MainBlock;
